<template>
  <span>Loading</span>
</template>
<script>
import Config from "@/config";
import { mapActions, mapMutations, mapState } from "vuex";
import tools from "@/utils/tools";
export default {
  methods: {
    ...mapActions({
      saveCodeAndOpenId: "app/saveCodeAndOpenId"
    })
  },
  mounted() {
    const isAuthed = localStorage.getItem("isAuthed");
    const p = window.location.search;
    const s = tools.parseUrl(p);

    const code = s.code;
    const openid = s.openid;
    console.log("有了没的：", s);
    // 已经有了code，存全局
    if (code) {
      this.saveCodeAndOpenId({ code, openid });
      this.$router.replace("/home");
      localStorage.setItem("isAuthed", true);
      return;
    }
    let scope = "snsapi_userinfo";
    if (isAuthed) {
      scope = "snsapi_base";
    }
    const href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${
      Config.appId
    }&redirect_uri=${encodeURIComponent(
      window.location.origin
    )}&response_type=code&scope=${scope}&state=STATE#wechat_redirect`;
    top.location.href = href;
  }
};
</script>