import Server from '../utils/server';
import axios from 'axios';
import Config from '../config';
const Module = {
  namespaced: true,
  state: {
    wxConfigData: {},
    wxToken: '',
    wxCode: '',
    wxOpenId: '',
  },
  mutations: {
    saveCodeAndOpenId(state, payload) {
      state.wxCode = payload.code || '';
      state.wxOpenId = payload.openid || '';
    },
    saveWxConfigData(state, payload) {
      state.wxConfigData = payload.data;
    },
    saveWxToken(state, payload) {
      state.wxToken = payload.data;
    },
  },
  actions: {
    /** 保存code和appid **/
    async saveCodeAndOpenId(context, payload) {
      context.commit({
        type: 'saveCodeAndOpenId',
        data: payload,
      });
    },
    /** 获取自定义的token **/
    async getToken({ state }, payload) {
      try {
        const url = `/wxChat/getToken.action`;
        const headers = {
          code: state.wxCode,
        };
        console.log('发起token请求：', headers, Config.baseUrl, url);

        const msg = await axios({
          url: `${Config.baseUrl}${url}`,
          method: 'GET',
          headers: {
            // 'Content-Type': 'application/json; charset=utf-8',
            ...headers,
          },
          withCredentials: false,
        });
        console.log('有返回token？', msg);
      } catch (e) {
        console.log('请求错误');
      }
    },
    /** 微信获取token **/
    async getWechartToken({ state, rootState }, payload) {
      try {
        if (!state.code) {
          console.log('未经过微信授权');
          return false;
        }
        const url = `/wxChat/getUser.action`;

        const msg = await axios({
          url: `${Config.baseUrl}${url}`,
        });

        if (msg.status === 200 || msg.status === 304) {
          context.commit({
            type: 'saveWxToken',
            data: msg.data,
          });
          return msg;
        }
        return msg;
      } catch (e) {
        console.log('请求错误');
      }
    },
    /** 微信初始化所需信息 **/
    async getWechartConfig(context, payload) {
      console.log('ajax11:', payload);
      try {
        const url = `/wxChat/getSignature.action`;

        const msg = await axios.get(`${Config.baseUrl}${url}?url=${encodeURI(payload)}`);
        console.log('有返回？', msg);
        if (msg.status === 200 || msg.status === 304) {
          context.commit({
            type: 'saveWxConfigData',
            data: msg.data,
          });
          return msg;
        }
        return msg;
      } catch (e) {
        console.log('请求错误');
      }
    },
  },
};

export default Module;
